import React from "react";
import Results from "../Results/Results";
import "./ResultsBlock.css";

const results = [
  { number: 15, sense: " " },
  { number: 20, sense: "   " },
  { number: 100, sense: " -    " },
  { number: 150, sense: " " },
];
const ResultsBlock = () => {
  return (
    <div className="build__heading__block">
      <div className="container">
        <h1 className="build__heading"> </h1>
        <div className="build__statistics__block">
          {results.map(({ number, sense }) => {
            return <Results key={sense} number={number} sense={sense} />;
          })}
        </div>
        <div className="link__bar"></div>
      </div>
    </div>
  );
};

export default ResultsBlock;
